<template>
  <div>
        <el-form ref="addForm" :rules="rules" :model="dataForm" label-width="150px">
            <el-row>
                <el-col :span="22">
                    <el-form-item label="名称" prop="typeName">
                        <el-input type="text" v-model="dataForm.typeName" maxlength='15' placeholder="请输入名称"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <div class="dialog-footer">
                <el-button type="primary" size="small" @click="saveArea()">保存</el-button>
                <el-button type="danger" size="small" @click="cancle()">取消</el-button>
            </div>
        </el-form>
    </div>
</template>

<script>
    export default{
        name:'add-area-dialog',
        props: [
                'checkedData',
                'isEdit'//true：添加，false 编辑
        ],
        data(){
            return{
                //表单验证
                rules: {
                    typeName: [{
                        required: true,
                        message: '请输入菜单代码',
                        trigger: 'change'
                    }],
                },
                dataForm:{
                    typeName: "",
                    typeId:'',
                },
               
            }
        },
        created(){ 
            this.setFormData()
        },
        methods:{
            setFormData(){
                if(this.isEdit === false){
                    this.dataForm.typeName = this.checkedData.name
                    this.dataForm.typeId = this.checkedData.id
                } 
            },
            //添加保存
            saveArea(){
                this.$refs['addForm'].validate((valid) => {
                    if(valid) {
                        this.$http({
                            url: this.$http.adornUrl(this.API.savaOrUpdateType),
                            method: 'post',
                            data: this.$http.adornData(
                                this.dataForm
                            )
                        }).then((res) =>{
                            var resData = res.data;
                            if(resData.code == 0) {
                                this.$message({
                                    message: '成功',
                                    type: 'success'
                                });
                                this.$emit("addback",true);
                            } else {
                                this.$message.error(resData.msg);
                            }
                        })
                    } 
                })
            },
            //取消
            cancle() {
                this.$emit("addback",false);
            } 
        }
    }
</script>

<style>
</style>



